.SkillPage {
  width: 100%;
  margin: auto;
  
  padding-top: 50px;
  
  padding-bottom: 50px;


}

.Skillcards {
  width: 80%;
  display: grid;
  grid-template-columns: repeat(6,1fr);
  margin: auto;
  /* display: flex; 
  justify-content: center;
  flex-wrap: wrap;  */
}
.Skillcards > div {
  flex: 0 0 33%; /* Each div takes 33% width to show three images in a row */
  padding: 10px; /* Add some padding between images */
  /* box-sizing: border-box; */
  color: white;
}

.Skillcards > div img {
  background: #55367d;
  background: linear-gradient(135deg, #9100ca, #ffffff);
  padding: 20px;
  border-radius: 100%;
  height: 100px; 
  width: 100px;
  /* Set a fixed height for consistent size */
  object-fit: cover;
  /* object-position: center; */
}
@media (max-width: 950px) {
  
.Skillcards {
  width: 80%;
  display: grid;
  grid-template-columns: repeat(2,1fr);
  margin: auto;
  /* display: flex; 
  justify-content: center;
  flex-wrap: wrap;  */
}
.Skillcards > div {
  flex: 0 0 33%; /* Each div takes 33% width to show three images in a row */
  padding: 10px; /* Add some padding between images */
  /* box-sizing: border-box; */
  color: white;
}

.Skillcards > div img {
  background: #55367d;
  background: linear-gradient(135deg, #9100ca, #ffffff);
  padding: 20px;
  border-radius: 100%;
  height: 50px; 
  width: 50px;
  /* Set a fixed height for consistent size */
  object-fit: cover;
  /* object-position: center; */
}
}